
import XTable from 'vux/src/components/x-table/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import Group from 'vux/src/components/group/index.vue'

export default {
  components: {
    XTable,
    XSwitch,
    Group
  },
  name: 'manage',
  data () {
    return {
      msg: '插件管理',
      plugins: [],
      currentIndex: 1,
      uuid: localStorage.uuid || null
    }
  },
  methods: {
    changeEnable (name, v, index) {
      if (v) {
        console.log(v)
        this.$ajax({
          method: 'PATCH',
          url: 'http://localhost:8080/enable',
          params: {
            uuid: this.uuid,
            name: name
          }
        }).then(res => {
          console.log(res)
        }, res => {
          console.log('enable error', res)
        })
      } else {
        this.$ajax({
          method: 'PATCH',
          url: 'http://localhost:8080/disable',
          params: {
            uuid: this.uuid,
            name: name
          }
        }).then(res => {
          console.log(res)
        }, res => {
          console.log('enable error', res)
        })
      }
    },
    getPlugins () {
      this.$ajax({
        method: 'get',
        url: 'http://localhost:8080/status',
        params: {
          uuid: this.uuid
        }
      }).then(res => {
        this.plugins = res.data.plugins
      }, res => {
        this.$router.push('/')
        console.log('get error', res)
      })
    }
  },
  mounted () {
    this.getPlugins()
  }
}



